.masthead {
  min-height: 30rem;
  position: relative;
  display: table;
  width: 100%;
  height: auto;
  padding-top: 8rem;
  padding-bottom: 8rem; }
  .masthead:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url("/images/developer.svg");
    background-color: #3e5871;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover; }
  @media (min-width: 992px) {
    .masthead {
      height: 100vh; } }
  .masthead .container h1 {
    font-size: 4rem;
    margin: 0;
    padding: 0;
    text-shadow: 1px 1px 0 rgba(255, 255, 255, 0.5), 1px 1px 0 rgba(0, 0, 0, 0.25), 3px 3px 3px rgba(0, 0, 0, 0.25), 10px 10px 20px rgba(0, 0, 0, 0.5), 20px 40px 40px rgba(0, 0, 0, 0.5);
    mix-blend-mode: multiply; }
  @media (min-width: 992px) {
    .masthead .container h1 {
      font-size: 5.5rem; } }
  .masthead .container h3.mb-5 {
    margin-top: 40px;
    text-shadow: 1px 1px 0 rgba(255, 255, 255, 0.5), 1px 1px 0 rgba(0, 0, 0, 0.25), 3px 3px 3px rgba(0, 0, 0, 0.25), 10px 10px 20px rgba(0, 0, 0, 0.5), 20px 40px 40px rgba(0, 0, 0, 0.5);
    mix-blend-mode: multiply;
    font-size: 2.3rem; }
  .masthead .find-more {
    height: 40px;
    width: 50px;
    display: block;
    position: absolute;
    bottom: 0px;
    right: 15px;
    background-color: #222222;
    text-align: center;
    z-index: 6;
    border-radius: 5px 5px 0 0;
    color: #343a40;
    cursor: pointer; }
    .masthead .find-more i {
      animation-name: wiggle;
      animation-duration: 1.2s;
      animation-timing-function: linear;
      animation-iteration-count: infinite;
      animation-direction: normal; }

@keyframes wiggle {
  0% {
    padding-top: 4px; }
  50% {
    padding-top: 12px; }
  100% {
    padding-top: 4px; } }

